import { useResize } from "../../hook/useResize";
import { useEffect, useState } from "react";

export const Twitch = () => {
  const { isScreenL } = useResize();
  const [size, setSize] = useState("30");

  useEffect(() => {
    const svgSize = isScreenL ? "30" : "25";
    setSize(svgSize);
  }, [isScreenL]);

  return (
    <svg
      width={size}
      height={size}
      viewBox="0 0 30 30"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <g clipPath="url(#clip0_12_228)">
        <path
          d="M1.99095 -0.00480652L-0.00378418 5.14475V26.1916H7.08713V29.9952H11.0766L14.8407 26.1916H20.6062L28.3599 18.3568V-0.00480652H1.99095ZM25.7024 17.0108L21.2689 21.4907H14.178L10.4139 25.2943V21.4907H4.42969V2.68046H25.7024V17.0108ZM21.2689 7.83001V15.6581H18.6115V7.83001H21.2689ZM14.178 7.83001V15.6581H11.5206V7.83001H14.178Z"
          fill="black"
        />
      </g>
      <defs>
        <clipPath id="clip0_12_228">
          <rect width="28.3636" height="30" fill="white" />
        </clipPath>
      </defs>
    </svg>
  );
};
