import { useResize } from "../../hook/useResize";
import { useEffect, useState } from "react";

export const Wysiwyg = () => {
  const { isScreenL } = useResize();
  const [size, setSize] = useState("32");

  useEffect(() => {
    const svgSize = isScreenL ? "32" : "24";
    setSize(svgSize);
  }, [isScreenL]);

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={size}
      height={size}
      viewBox="0 0 32 32"
      fill="none"
    >
      <g clipPath="url(#clip0_15_51)">
        <path
          d="M25.3333 4H6.66667C5.18667 4 4 5.2 4 6.66667V25.3333C4 26.8 5.18667 28 6.66667 28H25.3333C26.8 28 28 26.8 28 25.3333V6.66667C28 5.2 26.8133 4 25.3333 4ZM25.3333 25.3333H6.66667V9.33333H25.3333V25.3333ZM22.6667 16H9.33333V13.3333H22.6667V16ZM17.3333 21.3333H9.33333V18.6667H17.3333V21.3333Z"
          fill="#F8F200"
        />
      </g>
      <defs>
        <clipPath id="clip0_15_51">
          <rect width="32" height="32" fill="white" />
        </clipPath>
      </defs>
    </svg>
  );
};
